.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-exception {
  display: flex;
  align-items: center;
  flex-flow: column;
}
.RA-exception h1 {
  margin-bottom: 24px;
  color: #434e59;
  font-weight: 600;
  font-size: 72px;
  line-height: 72px;
  margin-top: 200px;
}
.RA-exception p {
  margin-bottom: 16px;
  color: var(--text-color-secondary);
  font-size: 20px;
  line-height: 28px;
}
.RA-exception > span {
  color: var(--text-color-secondary);
}
.RA-exception button {
  margin-left: 16px;
}
