.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.slide-enter {
  opacity: 0;
  -webkit-transform: translateX(-20px);
          transform: translateX(-20px);
}
.slide-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: all 800ms ease-out;
  transition: all 800ms ease-out;
}
.slide-exit {
  opacity: 0;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}
.slide-exit-active {
  opacity: 1;
  -webkit-transition: all 800ms ease-out;
  transition: all 800ms ease-out;
}
.fadeRA-enter {
  opacity: 0;
}
.fadeRA-enter-active {
  opacity: 1;
  -webkit-transition: opacity 800ms ease-out;
  transition: opacity 800ms ease-out;
}
.fadeRA-exit {
  opacity: 1;
}
.fadeRA-exit-active {
  opacity: 0;
  -webkit-transition: opacity 800ms ease-out;
  transition: opacity 800ms ease-out;
}
.shrink-enter {
  opacity: 0;
  -webkit-transform: translate3d(50px, 15px, -50px);
          transform: translate3d(50px, 15px, -50px);
}
.shrink-enter-active {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: all 500ms ease-out;
  transition: all 500ms ease-out;
}
.shrink-exit {
  opacity: 1;
}
.shrink-exit-active {
  opacity: 0;
  -webkit-transform: translate3d(50px, 15px, -50px);
          transform: translate3d(50px, 15px, -50px);
  -webkit-transition: all 500ms ease-out;
  transition: all 500ms ease-out;
}
